"use client";

import { useEffect, useState } from "react";
import { motion } from "framer-motion";

export default function PensionHero() {
    const [balance, setBalance] = useState(124.50);

    useEffect(() => {
        // Determine tick rate to be smoother
        const interval = setInterval(() => {
            setBalance((prev) => prev + 0.000005);
        }, 16); // roughly 60fps
        return () => clearInterval(interval);
    }, []);

    return (
        <div className="flex flex-col items-center justify-center py-20 text-center space-y-6">
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className="flex items-center gap-2 text-emerald-400 text-xs font-bold uppercase tracking-widest bg-emerald-500/5 px-4 py-1.5 rounded-full border border-emerald-500/10"
            >
                <div className="relative flex h-2 w-2">
                    <span className="animate-ping absolute inline-flex h-full w-full rounded-full bg-emerald-400 opacity-75"></span>
                    <span className="relative inline-flex rounded-full h-2 w-2 bg-emerald-500"></span>
                </div>
                Streaming Interest Live
            </motion.div>

            <div className="space-y-2">
                <h2 className="text-gray-400 font-medium text-sm">Total Pension Balance</h2>
                <div className="text-7xl md:text-9xl font-black text-white tracking-tighter tabular-nums leading-none">
                    ${balance.toFixed(6)}
                </div>
            </div>

            <motion.p
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ delay: 0.5 }}
                className="text-gray-500 max-w-md"
            >
                Your gig work is automatically fueling your future. <br />
                <span className="text-gray-300 decoration-emerald-500 underline decoration-1 underline-offset-4">12 deliveries</span> completed today.
            </motion.p>
        </div>
    );
}
